'use strict';
import '../css/upload.css';
import DocumentTitle from'react-document-title';
import util from '../utils/utils.js';
import Photo from '../components/photo.js';
import Label from '../components/label.js';
import API_Location from '../service/location.service.js';
import API_Upload from '../service/upload.service.js';
/**
 * https://github.com/igorprado/react-notification-system
 */
import NotificationSystem from 'react-notification-system';

class Upload extends React.Component{

  constructor(props) {
    super(props);
    this.state = {
      filesArr: [],                     // file 
      fileInfo:{                        // file
        number:0,                       // 
        size:0                          // 
      },
      imgBase:[],                       // img base64 
      labeList:[],                      // 
      beSelectCity:'',                  //  
      hiddenId:'',                      // labelid
      userInfo:{}
    }

    this._notificationSystem = null;
    //this._selectCity = this._selectCity.bind(this)
  }


  componentWillMount() {
    /**
     * userInfo
     */
    let userInfo = sessionStorage.getItem('userinfo.binlin.site');

    this.setState({
      userInfo: JSON.parse(userInfo)
    });
  }

  componentDidMount() {

    this._notificationSystem = this.refs.notificationSystem;

    /**
     * label
     * @param  {[type]} (err,res [description]
     * @return {[type]}          [description]
     */
    console.log(this.state.userInfo)
    API_Location.getLocation({openId:this.state.userInfo.openid}, (err,res) => {
      if(err){
        this.notify({
          title:'Tip',
          message:err,
          level:'error'
        })
        return;
      }
      if(res){
        this.setState({labeList: res.location})
        this.setState({hiddenId: res._id})
      }
    })
  }

  /**
   * {
   *   title:'Notification title',
   *   message: 'Notification message',
   *   level: 'success'
   * }
   */
  notify(obj){
    this._notificationSystem.addNotification(obj);
  }

  chooseImg(e){                        // 
    let et = e.target.files;
    this.resetState(et);
  }

  addImg(e){                           // 
    let et = e.target.files;
    this.resetState(et);
    let storeFiles = _.union(this.state.filesArr,et);  // statefile
    console.log(storeFiles);
    this.setState({filesArr: storeFiles});
  }

  /**
   *  21label 2label
   * @return {[type]} [description]
   */
  uploadImg(){

    // STEP ONE
    let uploadFileFormData = new FormData(),
        uploadPermission = true;

    uploadFileFormData.append('openId',this.state.userInfo.openid);

    /**
     * label 
     * @type {[type]}
     */
    this.state.beSelectCity == '' ? 
    this.notify({
      title:'Tip',
      message:'',
      level:'error',
      onAdd:()=>{
        uploadPermission =false;
      }
    })
    : uploadFileFormData.append('city',this.state.beSelectCity);

    this.state.filesArr.length == 0 ? 
    this.notify({
      title:'Tip',
      message:'',
      level:'error',
      onAdd:()=>{
        uploadPermission =false;
      }
    })
    : _.map(this.state.filesArr, (file)=>{                     // 
        uploadFileFormData.append('file',file)
      });

    uploadPermission ?
    
    (function(_this){
      //STEP TWO
      let labelOpt = {};
      labelOpt.location = _this.state.labeList;
      labelOpt.openId = _this.state.userInfo.openid;

      if(_this.state.hiddenId !== '') {
        labelOpt.id = _this.state.hiddenId;
      }
      
      API_Location.setLocation (labelOpt, (err, res)=>{
        console.log(res)
      })
      //STEP ONE
      API_Upload.uploadtoqiniu(uploadFileFormData, (err, res)=>{
        if(err){
          console.error(err);
          _this.notify({
            title:'Tip',
            message:' '+ err.msg,
            level:'error'
          })
          return;
        }
        console.log(res)
        _this.notify({
          title:'Tip',
          message:res.msg,
          level:'info'
        })
        _this.initData();
      }) 

    }(this))
    : '';

  }

  initData(){
    this.setState({
      imgBase:[],
      filesArr: [],                    
      fileInfo:{                       
        number:0,                      
        size:0                         
      },
    })
  }


 /**
  * [resetState description]
  * @param  {[type]} et []
  * @return {[type]}    [description]
  * filesArr,uploadImg
  */
  resetState(et){                                           // filesArr
    let fileInfo = {
      number:0,                       
      size:this.state.fileInfo.size
    }

    this.setState({filesArr: et},()=>{                      // 
      fileInfo.number = this.state.filesArr.length;
      _.map(et, (file) => {
        fileInfo.size = fileInfo.size+ file.size/1000000;   // M
        this.file2canvas(file);
      })
      this.setState({fileInfo:fileInfo})
    });
  }

  /**
   * TODO 
   */
  file2canvas(files){
    util.readBlobAsDataURL(files, (dataurl)=>{
      let storeImg = this.state.imgBase;                    // 
      storeImg.push(dataurl);                               // push 
      this.setState({imgBase:storeImg})                     // states
    })
  }

  /**
   * [_selectCity description]
   * @param  {[type]} i []
   * @return {[type]}   [description]
   */
  _selectCity = (i) => {
    let labeList = this.state.labeList;
    let beSelectCity;
    _.map(labeList, (list, index)=>{
      if(i == index) {
        list.actived = true;
        beSelectCity = list.city;
      } else {
        list.actived = false;
      }
    });

    this.setState({
      labeList:labeList,                                    // setStaterender
      beSelectCity:beSelectCity
    })                                   
  }

  /**
   * state.labeList
   * @param  {[type]} e [description]
   * @return {[type]}   [description]
   */
  _addCity = (e) => {
    let labeList = this.state.labeList; 
    let label = e.target.value;
    label == "" ? '' :
    labeList.push({'city':e.target.value})
    this.setState({
      labeList:labeList
    })
  }

  render(){
    let {imgBase, fileInfo, labeList, beSelectCity, hiddenId} = this.state;

    /**
     * classnames className
     * @type {[type]}
     */
    let uploadWp = classNames({
      'queueList': true,
      'placeholder-hide': this.state.filesArr.length != 0             // 
    });

    let showWp = classNames({
      'queueList': true,
      'filled': true,
      'placeholder-hide': this.state.filesArr.length == 0             //  
    });
    
    return (
      <div className="container-fluid">
        <Label labelList={labeList} _selectCity={this._selectCity} _addCity={this._addCity}></Label>
        <input type="hidden" value={hiddenId} />
        <div className="wu-example" id="uploader">
          <div className={uploadWp}>
            <div className="placeholder">
              <div className="webuploader-container">
                <div className="webuploader-pick">
                  
                </div>
                  <div className="file-wp">
                    <label className="file-label">
                      <input type="file" className="webuploader-element-invisible" multiple="multiple" accept="image/jpg,image/jpeg,image/png" onChange={ (e)=>this.chooseImg(e) } />
                    </label>
                  </div>
              </div>
              <div className="filelist">

              </div>
            </div>

          </div>

          <div className={showWp}>
            <ul className="filelist">
              {
                imgBase.map(function(obj, index){
                  return <Photo key={index} src={obj}></Photo>
                })
              }
            </ul>
          </div>

          <div className="statusBar">
            <div className="info">{fileInfo.number},{fileInfo.size.toFixed(2)}M</div>
            <div className="btns">
              <div className="webuploader-container">
                <div className="webuploader-pick fl"></div>
                <div className="file-wp-status">
                  <label className="file-labels">
                    <input type="file" className="webuploader-element-invisible" multiple="multiple" accept="image/jpg,image/jpeg,image/png" onChange={ (e)=>this.addImg(e) }/>
                  </label>
                </div>
                <div className="uploadBtn state-ready fl" onClick={ (e)=>this.uploadImg() }></div>
              </div>
            </div>
          </div>
        </div>
        <NotificationSystem ref="notificationSystem" />
      </div>
    );
  }
};

export default Upload;




