'use strict';
import '../css/albumlogin.css';
import '../css/react-notification-system.css';
import DocumentTitle from'react-document-title';
import {Helmet} from "react-helmet";
import API_Login from "../service/login.service.js"
import NotificationSystem from 'react-notification-system';
import util from '../utils/utils.js';

class Login extends React.Component{
  constructor(props) {
    super(props);
  
    this.state = {
      isLoginAction:true,
      confirm:'',
      userRegisterInfo:{
        userName:'',
        password:'',
        confirmPassword:'',
        userPhone:''
      },
      isAllowSending:false,        // 
      isSending:false,             // 
      confirmTimer:undefined       
    };
  }

  componentDidMount() {
    this._notificationSystem = this.refs.notificationSystem;
    let isreg = this.props.params.isreg == 'register';
    isreg ? this.toRegister() : this.backLogin();
  }

  /**
   * propscomponentWillReceivePropsnextPropsprops
   * @param  {[type]} nextProps [description]
   * @return {[type]}           [description]
   */
  componentWillReceiveProps(nextProps){
    let isreg = nextProps.params.isreg == 'register';
    isreg ? this.toRegister() : this.backLogin()
  }

  /**
   * {
   *   title:'Notification title',
   *   message: 'Notification message',
   *   level: 'success'
   * }
   */
  notify(obj){
    this._notificationSystem.addNotification(obj);
  }

  /**
   * 
   * @return {[type]} [description]
   */
  toRegister(){
    this.setState({isLoginAction:false})
  }

  /**
   * 
   * @return {[type]} [description]
   */
  backLogin(){
    this.setState({isLoginAction:true})
  }

  /**
   * 
   * @return {[type]} [description]
   */
  getConfirmcode(time){
    let {isAllowSending, isSending} = this.state;
    if(!isAllowSending) {
      this.notify({
        message:'',
        level:'warning',
        autoDismiss:'2'
      })
      return;
    }

    if(isSending){
      this.notify({
        message:'^.^',
        level:'warning',
        autoDismiss:'2'
      })
      return;
    }
    let phoneObj = {};
    let runtimes = time;
    this.runtime(runtimes)
    phoneObj = {
      phone:this.refs.phone.value
    }
    
    API_Login.getConfirm(phoneObj, (err, res) => {
      if(err){
        this.notify({
          message: '',
          level: 'error'
        })
        return;
      }
      this.setState({isSending:true})
      this.notify({
        message: '',
        level:'success'
      })
    })
  }

  /**
   *    
   */
  runtime(time){

    if(time == 0){
      this.setState({'confirm':''})
      clearTimeout(this.confirmTimer);
      this.setState({isSending:false})
      return;
    }
    
    this.confirmTimer = setTimeout(() => {
      time--;
      this.setState({'confirm':time+''})
      this.runtime(time)
    }, 1000)
    
  }

  /**
   * user
   * @param  {[type]} flag [description]
   * @return {[type]}      [description]
   */
  async confirmUsername(flag){
    let isConfirm,
        username = this.refs.username.value;
    if(username == '') {
      this.notify({
        message: '',
        level: 'warning',
        autoDismiss:'2'
      })
      isConfirm = false;
    } else {
      // API_Login.confirmname({
      //   "username":username
      // },(err, res) => {
      //   if(err){
      //     this.notify({
      //       message: err.msg,
      //       level: 'error',
      //       autoDismiss:'2'
      //     })
      //     return
      //   }

      //   if(res.msg == 'valid' && flag) {
      //     isConfirm = true;
      //     this.notify({
      //       message: '',
      //       level: 'success',
      //       autoDismiss:'2'
      //     })
      //   } else {

      //     isConfirm = false;
      //     this.notify({
      //       message: '',
      //       level: 'warning',
      //       autoDismiss:'2'
      //     })
      //   }

      //   console.log(isConfirm)
      //   return isConfirm
      // })
      
      try {
        let msg = await API_Login.confirmname({"username":username});
        if(msg == 'valid' && flag) {
          isConfirm = true;
          this.notify({
            message: '',
            level: 'success',
            autoDismiss:'2'
          })
        } else if(msg == 'invalid'){
          isConfirm = false;
          this.notify({
            message: '',
            level: 'warning',
            autoDismiss:'2'
          })
        } else if (msg == 'valid') {
          isConfirm = true;
        }
      } catch(err) {
        isConfirm = false;
      }
      console.log(isConfirm,'----')
      return isConfirm;
    };
    
  }

  /**
   *  
   * @flag  bluror
   * @return {[type]} [description]
   */
  confirmPassword(flag){
    /**
     * isConfirm  2
     */
    let isConfirm,
        firstPassword = this.refs.firstPassword.value,
        secondPassword = this.refs.secondPassword.value;

    if (firstPassword == '') {
      this.notify({
        message: '',
        level: 'warning',
        autoDismiss:'2'
      });
      isConfirm = false
      return;
    }
    /**
     * flag 
     * @param  {[type]} _this) {                 firstPassword [description]
     * @return {[type]}        [description]
     */
    //TODO bug
    flag ? (function(_this) {
      console.log(firstPassword == secondPassword)
      firstPassword == secondPassword ? isConfirm = true : (function(){
        _this.notify({
          message: '',
          level: 'warning',
          autoDismiss:'2'
        })
        isConfirm = false;
      })();
    }(this)) : isConfirm = true;
    return isConfirm;
  }
  /**
   * 
   * @return {[type]} [description]
   */
  checkMobile(){
    let isConfirm = false,
        mobilePhone = this.refs.phone.value;
    util.checkMobile(mobilePhone) ? isConfirm = true : this.notify({
      message: '',
      level: 'warning',
      autoDismiss:'2'
    })
    return isConfirm;
  }
  /**
   *   
   * @return {[type]} [description]
   */
  watchPhone(e){
    let mobilePhone = e.target.value;
    util.checkMobile(mobilePhone) ? this.setState({
      isAllowSending:true
    }) : this.setState({
      isAllowSending:false
    });
  }

  /**
   * 
   */
  login(){
    let logininfo = {
      username:this.refs.usernameLogin.value,
      password:this.refs.passwordLogin.value
    }
    API_Login.login(logininfo, (err, res)=>{
      if(err) {
        let msg = err.msg;
        this.notify({
          message:msg,
          level:'error',
          autoDismiss:'2'
        })
      }

      if(res.status){
        this.notify({
          message:res.msg,
          level:'success',
          autoDismiss:'2'
        }) 
      } else {
        this.notify({
          message:res.msg,
          level:'error',
          autoDismiss:'2'
        })
      } 
      
    })
  }

  /**
   * 
   * @return {[type]} [description]
   */
  register(){
    let cpUsername,cpPassword,cpMobilephone;
    /**
     * async promise
     */
    this.confirmUsername().then(val => {
      cpUsername = val
      cpPassword = this.confirmPassword(true),
      cpMobilephone = this.checkMobile()
      console.log(cpUsername)
      if(!cpUsername || !cpPassword || !cpMobilephone){
        return;
      }

      let userInfo = {
        username:this.refs.username.value,
        password:this.refs.firstPassword.value,
        phone:this.refs.phone.value,
        code:this.refs.code.value,
        registerDate:new Date()
      }
      console.log(userInfo)
      API_Login.register(userInfo, (err, res)=>{
        console.log(err,res)
        if(err){
          let msg = err.msg;
          this.notify({
            message:msg,
            level:'error',
            autoDismiss:'2'
          })
        }

        this.notify({
          message:res.msg,
          level:'success',
          autoDismiss:'2'
        })
        this.initRegister();
      })
    });
  }

  /**
   * 
   */
  initRegister(){
    this.refs.username.value = '';
    this.refs.firstPassword.value = '';
    this.refs.secondPassword.value = '';
    this.refs.phone.value = '';
    this.refs.code.value = '';
    this.setState({'confirm':''})
    clearTimeout(this.confirmTimer);
    this.setState({isSending:false})
  }

  render(){
    let {isLoginAction, confirm, isAllowSending} = this.state;

    let login = classNames({
      "login-form":true,
      "active":isLoginAction
    })

    let register = classNames({
      "register-form":true,
      "initpos":true,
      "active":!isLoginAction
    })

    let getConfirmCode = classNames({
      "confirm-input":true,
      "confirm-active":!isAllowSending
    })

    return (
      <DocumentTitle title='Login'>
        <div>
          <div className="login-wp">
            <h3 className="login-title">Binlin Album</h3>
            <ul className={login}>
              <li>
                <i className="icon-fz ion-ios-person-outline"></i>
                <div className="login-input-wp">
                  <input ref="usernameLogin" type="text" className="login-input" placeholder=""/>
                </div>
              </li>
              <li>
                <i className="icon-fz ion-ios-locked-outline"></i>
                <div className="login-input-wp">
                  <input ref="passwordLogin" type="password" className="login-input" placeholder=""/>
                </div>
              </li>
              <li className="noline">
                <p className="half-wp col-left"></p>
                <p className="half-wp col-right fpw"></p>
              </li>
              <li className="signin noline">
                <a className="signin-btn" onClick={()=>this.login()}></a>
              </li>
              <li className="noline register" onClick={()=>{this.toRegister()}}>
                <a> <span></span></a>
              </li>
            </ul>
            <ul className={register}>
              <li>
                <i className="icon-fz ion-ios-person-outline"></i>
                <div className="login-input-wp">
                  <input ref="username" type="text" className="login-input" placeholder="" onBlur={()=>this.confirmUsername(true)} />
                </div>
              </li>
              <li>
                <i className="icon-fz ion-ios-locked-outline"></i>
                <div className="login-input-wp">
                  <input ref="firstPassword" type="password" className="login-input" placeholder="" onBlur={()=>this.confirmPassword()} />
                </div>
              </li>
              <li>
                <i className="icon-fz ion-ios-locked-outline"></i>
                <div className="login-input-wp">
                  <input ref="secondPassword" type="password" className="login-input" placeholder="" onBlur={()=>this.confirmPassword(true)}/>
                </div>
              </li>
              <li>
                <i className="icon-fz ion-iphone"></i>
                <div className="login-input-wp">
                  <input ref="phone" type="number" className="login-input phone-input" placeholder="" onInput={(e)=>this.watchPhone(e)}  onBlur={()=>this.checkMobile()} />
                  <a className={getConfirmCode} onClick={()=>{this.getConfirmcode(60)}}>{confirm}</a>
                </div>
              </li>
              <li>
                <i className="icon-fz ion-chatbox-working"></i>
                <div className="login-input-wp">
                  <input ref="code" type="text" className="login-input" placeholder=""/>
                </div>
              </li>
              <li className="signin noline">
                <a className="signin-btn" onClick={()=>{this.register()}}></a>
              </li>
              <li className="noline">
                <p className="half-wp col-left"></p>
                <p className="half-wp col-right fpw" onClick={()=>{this.backLogin()}}></p>
              </li>
            </ul>
          </div>
          <NotificationSystem ref="notificationSystem" style={false} />
        </div>
      </DocumentTitle>
    )
  }
}

export default Login;


